.contacts_list_title{
    font-size: 24px;
  margin-bottom: 20px;
 
  color: #333;
  text-transform: uppercase;
}

.contacts_list{
    font-size: 18px;
    list-style: none;
    padding: 0;
}

.contacts_list_item{
    margin-bottom: 20px;
  
}

.contacts_list_delete_btn{

  padding: 8px;
  background-color: #378c2a;
  color: #fff;
  border: none;
  border-radius: 5px;
  font-size: 18px;
  cursor: pointer;
  transition: background-color 0.8s ease;
}

.contacts_list_delete_btn:hover {
 background-color: #f40b0beb; 
  transition: background-color 0.6s ease;
}